import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import {
  Box,
  Typography,
  Paper,
  Button,
  TextField,
  Divider,
  CircularProgress,
  Alert,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions
} from '@mui/material';
import { useAuth } from '../hooks/useAuth';
import { postAPI } from '../services/api';
import { Post } from '../types';

const PostDetail: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const [post, setPost] = useState<Post | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [comment, setComment] = useState('');
  const [confirmDialogOpen, setConfirmDialogOpen] = useState(false);
  const { isAuthenticated, user } = useAuth();
  const navigate = useNavigate();

  useEffect(() => {
    const fetchPost = async () => {
      try {
        if (id) {
          const response = await postAPI.getPost(id);
          setPost(response.data);
        }
      } catch (err) {
        setError('');
      } finally {
        setLoading(false);
      }
    };

    fetchPost();
  }, [id]);

  const handleConfirmDelete = () => {
    setConfirmDialogOpen(true);
  };

  const handleCancelDelete = () => {
    setConfirmDialogOpen(false);
  };

  const handleDelete = async () => {
    try {
      if (id) {
        await postAPI.deletePost(id);
        setConfirmDialogOpen(false);
        navigate('/');
      }
    } catch (err) {
      setError('');
      setConfirmDialogOpen(false);
    }
  };

  const handleAddComment = async (e: React.FormEvent) => {
    e.preventDefault();

    try {
      if (id && comment.trim()) {
        const response = await postAPI.addComment(id, comment);
        setPost(response.data);
        setComment('');
      }
    } catch (err) {
      setError('');
    }
  };

  if (loading) {
    return <CircularProgress />;
  }

  if (!post) {
    return <Alert severity="error"></Alert>;
  }

  return (
    <Box>
      {error && (
        <Alert severity="error" sx={{ mb: 2 }}>
          {error}
        </Alert>
      )}

      <Paper sx={{ p: 3, mb: 3 }}>
        <Typography variant="h4" component="h1" gutterBottom>
          {post.title}
        </Typography>
        <Typography variant="subtitle1" color="text.secondary" gutterBottom>
          : {post.author.username} | : {new Date(post.createdAt).toLocaleDateString()}
        </Typography>
        <Divider sx={{ my: 2 }} />
        <Typography variant="body1" paragraph>
          {post.content}
        </Typography>

        {isAuthenticated && user && (post.author._id === user.userId || user.isAdmin) && (
          <Box sx={{ mt: 3, display: 'flex', gap: 2 }}>
            <Button
              variant="outlined"
              color="primary"
              onClick={() => navigate(`/edit/${post._id}`)}
            >
              
            </Button>
            <Button
              variant="outlined"
              color="error"
              onClick={handleConfirmDelete}
            >
              
            </Button>
          </Box>
        )}
      </Paper>

      <Box sx={{ mb: 3 }}>
        <Typography variant="h5" gutterBottom>
           ({post.comments.length})
        </Typography>
        {post.comments.map((comment, index) => (
          <Paper key={index} sx={{ p: 2, mb: 2 }}>
            <Typography variant="body1">{comment.content}</Typography>
            <Typography variant="caption" color="text.secondary">
              {`${comment.author.username} - ${new Date(comment.createdAt).toLocaleString()}`}
            </Typography>
          </Paper>
        ))}
      </Box>

      {isAuthenticated ? (
        <Box component="form" onSubmit={handleAddComment}>
          <Typography variant="h6" gutterBottom>
            
          </Typography>
          <TextField
            fullWidth
            label=""
            multiline
            rows={3}
            value={comment}
            onChange={(e) => setComment(e.target.value)}
            sx={{ mb: 2 }}
            required
          />
          <Button type="submit" variant="contained" disabled={!comment.trim()}>
            
          </Button>
        </Box>
      ) : (
        <Alert severity="info">
          <Button color="primary" onClick={() => navigate('/login')}></Button>
        </Alert>
      )}

      {/*  */}
      <Dialog
        open={confirmDialogOpen}
        onClose={handleCancelDelete}
      >
        <DialogTitle></DialogTitle>
        <DialogContent>
          <DialogContentText>
            
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCancelDelete}></Button>
          <Button onClick={handleDelete} color="error" autoFocus>
            
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default PostDetail; 